import React from 'react';
import PropTypes from 'prop-types';
import {Helmet} from 'react-helmet';

MetaSEO.defaultProps = {
    image: 'https://static.unica.vn/media/images/unica-hoctuchuyengia.jpg',
    title: 'Lê Văn Mong | Blogger MONGKER | Front-End Developer',
    name: 'Mongker',
    description: 'Nơi nói phép cùng Lê Văn Mong',
    icon: 'https://static.unica.vn/icon/favicon.ico',
    url: 'https://unica.vercel.app/',
};

MetaSEO.propTypes = {
    description: PropTypes.string,
    title: PropTypes.string,
    url: PropTypes.string,
    image: PropTypes.string,
    name: PropTypes.string,
};

/**
 * Hiển thị quản cáo meta cho nghiệp vụ SEO website
 * @param title
 * @param description
 * @param url
 * @param image
 * @param icon
 * @param name
 * @returns {JSX.Element}
 * @constructor
 */
function MetaSEO({ title, description, url, image, icon, name }) {
    return (
        <Helmet>
            <meta http-equiv='Content-Type' content='text/html; charset=utf-8' />
            <title>{title}</title>
            <meta name='description' content={description} />
            <link rel='icon' href={icon} />
            <meta name='author' content={name} />
            <meta name='keywords' content={description} />

            {/* ROBOTS */}
            <meta name='robots' content={'index,follow'} />

            {/* SEO google, facebook */}
            <meta property='og:description' content={description} />
            <meta property='og:url' content={url} />
            <meta property='og:title' content={title} />
            <meta property='og:type' content='article' />
            <meta property='og:image' content={image} />
            <meta property='og:image:width' content='720' />
            <meta property='og:image:height' content='480' />
        </Helmet>
    );
}

export default MetaSEO;
